import { reactive, computed } from 'vue';
import Vuex, { useStore } from 'vuex';
export default {
  name: 'Vue3',
  setup: function setup() {
    var store = useStore();
    var state = reactive({
      todo: '',
      todoList: computed(function () {
        return store.state.todoList;
      })
    });

    var pushTodoItem = function pushTodoItem(item) {
      return store.commit('PUSH_TODO_ITEM', item);
    };

    var removeTodoItem = function removeTodoItem() {
      return store.commit('REMOVE_TODO_ITEM');
    };

    var onSubmit = function onSubmit() {
      pushTodoItem({
        name: state.todo,
        date: new Date()
      });
      state.todo = '';
    };

    return {
      state: state,
      pushTodoItem: pushTodoItem,
      removeTodoItem: removeTodoItem,
      onSubmit: onSubmit
    };
  }
};